import React from "react";
import styled from "styled-components";

export const Input = ({ type, status, label}) => {
    return(
        <UIinput
            type={props.type}
            className={`input 
                ${props.success && 'input-success'}
                ${props.info && 'input-info'}
                ${props.alert && 'input-alert'}
                ${props.warning && 'input-warning'}
            `}
        >
            {label}
        </UIinput>
    )
}

const UIinput = styled.input``;

